<template>
	<div id="positionManagement">
		<el-table :data="tableData" style="width: 100%" height="650" border stripe>
			<el-table-column label="职位" min-width="12.5%" align="center">
				<template slot-scope="scope">
					<span v-if="!scope.row.entry_type||scope.row.entry_type=='1'">{{scope.row.position_name}}<span>(默认)</span></span>

					<span v-else>{{scope.row.position_name}}</span>
				</template>
			</el-table-column>
			<el-table-column label="权限" min-width="75%" align="center">
				<template slot-scope="scope">
					<span v-for="(item,index) in scope.row.arrRight">{{ item }}<span v-if="index<scope.row.arrRight.length-1">  ， </span></span>
				</template>
			</el-table-column>
			<el-table-column label="操作" min-width="12.5%" align="center">
				<template slot-scope="scope" v-if="scope.row.is_edit!==0">
					<router-link :to="{path:'/staff/editDuty',query:{infor:scope.row}}">
						<span size="small" type="text" class="blue">编辑
							</span>
					</router-link>
					<span size="small" type="text" @click="handleDelete(scope.row )" class="red" v-if="scope.row.entry_type=='2'">删除
						</span>
				</template>
			</el-table-column>
		</el-table>
			<!--分页弹框-->
			<div class="pagination-container">
				<el-pagination small @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="listQuery.page" :page-sizes="[20,30,50]" :page-size="listQuery.limit" layout="sizes, prev, pager, next, jumper" :total="total">
				</el-pagination>
			</div>
		
		<!-- 删除提示弹窗 -->
		<delete-dialog v-if="DeleteDialogVisible" @cancelClose="cancelClose" @confirmDelete="confirmDelete" :isStaff="isStaff" :isFreezed="false" :deleteValue="deleteValue"></delete-dialog>
		
		
	</div>
</template>

<script>
	import deleteDialog from "./children/children/deleteDialog"
	import { staffDataGet } from "../api";
	import { ls } from "@/config/pageStore";
	import { treeMethods , commonMethods} from "../index";
	export default {
		data() {
			return {
				DeleteDialogVisible: false,
				isStaff: false,
				deleteValue:"",
				total:10,
				positionName:[],
				listQuery: {
					page: 1,
					limit: 20
				},
				deleInfor: {},
				tableList: [{
					position_name: "管理员",
					arrRight: "全部",
					entry_type: 1,
					is_admin: 0
				}],
				tableData: [{
					position_name: "管理员",
					arrRight: "全部",
					entry_type: 1,
					is_admin: 0
				}]
			};
		},
		components: {
			deleteDialog
		},
		created() {
			this.initTableList();
			this.getEmployee();
		},
		methods: {
			//	初始化表格数据
			initTableList() {
				if(ls.getItem("staff#itemNum") != null) {
					this.listQuery.limit = Number(ls.getItem("staff#itemNum"));
				}
				let data = {
					get_position_list: 1
				}
				staffDataGet.getDutyList(data, (resp) => {
					this.tableData = resp.data.position_list;
					this.tableList = resp.data.position_list;
					this.tableData = treeMethods.isRight(this.tableData);
					this.tableList = treeMethods.isRight(this.tableList);
					//获取每页选择显示的条目数的历史记录
				
					this.tableData = commonMethods.getPageList(this.tableList, this.listQuery.limit, this.listQuery.page);
					this.total = commonMethods.getTotal(this.tableList);
				})
			},
			//获取员工的列表
			getEmployee() {
				staffDataGet.getStaffList({
					get_employee_list: 1
				}, (resp) => {
					let positionNamelist = resp.data.employee_list;
					positionNamelist.forEach((item)=>{
						if(this.positionName.indexOf(item.position_name)==-1) {
							this.positionName.push(item.position_name)
						}
						
					})
					
				})
			},
			//删除之后获取
			getDataAfter() {
				let data = {
					get_position_list: 1
				}
				staffDataGet.getDutyList(data, (resp) => {
					this.tableData = resp.data.position_list;
					this.tableData = treeMethods.isRight(this.tableData);
				})
			},
			//点击删除
			handleDelete(row) {
				this.deleteValue = row.position_name;
				if(this.positionName.indexOf(this.deleteValue)==-1) {
					this.DeleteDialogVisible = true;
					this.deleInfor = row
				}else{
					this.$slnotify({
		            message: "该部门包含员工信息，不能被删除",
		          });
				}
				
			},
			//点击确定删除
			confirmDelete() {
				this.DeleteDialogVisible = false;
				let data = {
					del_position: 1, //参数
					position_id: this.deleInfor.position_id
				}
				staffDataGet.editDutyRight(data, (resp) => {
					if(resp.ret == 0) {
						this.getDataAfter()
					}else {
						this.$slnotify({
							message: '操作失败'
						});
						return false;
					}
				})
			},
			//点击取消删除
			cancelClose() {
				this.DeleteDialogVisible = false;
				
			},
			
			//选择每页显示的条目数
			handleSizeChange(val) {
				this.listQuery.limit = val;
				ls.setItem("staff#itemNum", this.listQuery.limit);
				this.tableData = commonMethods.getPageList(this.tableList, this.listQuery.limit, this.listQuery.page);
				this.total = commonMethods.getTotal(this.tableList);
			},
			//选择当前页码
			handleCurrentChange(val) {
				this.listQuery.page = val;
				this.tableData = commonMethods.getPageList(this.tableList, this.listQuery.limit, this.listQuery.page);
				this.total = commonMethods.getTotal(this.tableList);
			},
		}
	};
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	.blue {
		font-size: 14px;
		color: #4877E7;
		cursor: pointer;
	}
	.mask{
		width: 150%;
		height: 150%;
		position: absolute;
		top: -230px;
		left: -230px;
		background: rgba(0,0,0,0.5);
		z-index: 9999;
	}
	#positionManagement {
		height: 770px;
		background: #FFFFFF;
	}

	.red {
		font-size: 14px;
		color: #E7487E;
		cursor: pointer;
	}
	.pagination-container {
		margin: 40px 0 40px 689px;
		font-family: MicrosoftYaHei;
		font-size: 12px;
		color: #666666;
		letter-spacing: 0.43px;
	}
	
	.pageSize {
		font-size: 12px;
		width: 70px;
		height: 34px;
		margin-right: 10px;
	}
</style>
<style lang="scss">
	@import "src/styles/mixin.scss";
	#positionManagement {
		.el-table thead .cell {
			font-weight: 900 !important;
		}
	}
</style>


