<template>
	<div id="deletDialog">
		<el-dialog class="delete-dialog" :visible.sync="DeleteDialogVisible" :show-close="false" size="" :before-close="handleClose">
			<div class="img"></div>
			<div class="dialog-content">
				<div class="delete-tip" v-if="isFreeze==false">
					<!--删除-->
					<div class="delete-text" v-if="success_delete==false">
						<span class="question"></span>
						<span>是否确定删除{{deleteTittle}}{{deleteName}}？</span>
					</div>
					<!--删除成功-->
					<div class="delete-text delete-success" v-if="success_delete==true">
						<span class="success-delete"></span>
						<span>删除成功</span>
					</div>
					
				</div>
				<!--冻结-->
				<div  class="delete-tip freezed-tip" v-if="isFreeze==true">
					<div class="delete-text">
						<span class="question freezed"></span>
						<span>冻结该员工信息，所属账号将不能使用，确定要冻结 ？</span>
					</div>
					</div>
				<div class="button-group clearfix" v-if="success_delete==true&&isFreeze==false">
					<div class="ok-btn left successDelete" @click="successDelete">确定</div>
				</div>

				<div class="button-group clearfix" v-if="success_delete==false&&isFreeze==false" >
					<div class="ok-btn left" @click="deleteConfirm">确定</div>
					<div class="cancel-btn left" @click="deleteCancel">取消</div>
				</div>
				<div class="button-group clearfix" v-if="isFreeze==true" >
					<div class="ok-btn left" @click="freezeConfirm">确定</div>
					<div class="cancel-btn left" @click="deleteCancel">取消</div>
				</div>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		props: ["isStaff","isFreezed","deleteValue"],
		data() {
			return {
				DeleteDialogVisible: true,
				isDuty: false,
				staff: "员工",
				duty: "职位",
				success_delete: false,
				isFreeze:false,
				deleteName:"",
				deleteTittle: ""
			}
		},
		created() {
			console.log(this.deleteValue)
			this.deleteTittle = this.isDuty !== this.isStaff ? this.staff : this.duty
			this.isDuty = this.isStaff;
			this.isFreeze = this.isFreezed;
			this.deleteName=this.deleteValue;
		},
		methods: {
			handleClose() {
				this.$emit("cancelClose")
			},
			//点击确定删除
			deleteConfirm() {
				if(this.isFreeze ==false) {
					this.success_delete = true;
					console.log(this.success_delete)
				}
			},
			//点击确定冻结
			freezeConfirm() {
				this.isFreeze = true;
				
				this.$emit("confirmIsfreeze")
				this.isFreeze = false;
			},
			//点击删除成功之后的确定
			successDelete() {
				this.$emit("confirmDelete")
			},
			//点击取消删除
			deleteCancel() {
				this.$emit("cancelClose")
			},
		}
	}
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	.delete-dialog {
		.dialog-content {
			@include wh(540px, 200px);
			.delete-tip {
				line-height: 80px;
				margin-left: 50px;
				margin-bottom: 40px;
				.delete-text {
					margin-left: 30px;
					display: inline-block;
					padding-bottom: 15px;
					padding-left: 30%;
					vertical-align: bottom;
					@include fc(14px, #666);
				}
				.delete-success {
					padding-left: 40%;
				}
			}
			.freezed-tip{
				line-height: 80px;
				margin-left: 50px;
				margin-bottom: 40px;
				.delete-text {
					margin-left: 30px;
					display: inline-block;
					padding-bottom: 15px;
					padding-left: 10%;
					vertical-align: bottom;
					@include fc(14px, #666);
				}
			}
		}
	}
	
	.button-group {
		margin-top: 40px;
		div {
			@include wh(160px, 40px);
			border-radius: 4px;
			border: 1px solid #5a8cff;
			color: #5a8cff;
			line-height: 40px;
			text-align: center;
			cursor: pointer;
		}
		.ok-btn {
			margin: 0 60px 0 77px;
			background-color: #5a8cff;
			color: #fff;
		}
	}
	
	.img {
		position: absolute;
		top: 0;
		background: url(../../image/topbar.png);
		width: 540px;
		height: 4px;
	}
	
	.question {
		position: absolute;
		display: inline-block;
		top: 75px;
		left: 150px;
		background: url(../../image/question_img_normal.png);
		width: 50px;
		height: 50px;
	}
	
	.success-delete {
		position: absolute;
		display: inline-block;
		top: 75px;
		left: 190px;
		/*margin-left: 50px;*/
		background: url(../../image/success_img_normal.png);
		width: 50px;
		height: 50px;
	}
	
	.successDelete {
		position: absolute;
		left: 130px;
	}
	.freezed{
		position: absolute;
		left: 50px;
	}
</style>

<style lang="scss">
	@import "src/styles/mixin.scss";
	#deletDialog {
		.el-dialog {
			top: 50%;
		}
		.el-dialog .el-dialog__header {
			padding: 0;
			margin-bottom: 60px;
		}
		.el-dialog__body {
			padding: 0;
		}
	}
</style>


